import React, {Component} from 'react';
import {Link} from "react-router-dom";

class NavMaterials extends Component {
    render() {
        return (
            <nav aria-label="breadcrumb">
                <ol className="breadcrumb">
                    <li className="breadcrumb-item"><Link to="/materials"></Link></li>
                    <li className="breadcrumb-item"><Link to="/materials/fill"></Link></li>
                    <li className=" breadcrumb-item"><a></a></li>
                    <li className=" breadcrumb-item"><a></a></li>
                    <li className=" breadcrumb-item"><a></a></li>
                    <li className=" breadcrumb-item"><a></a></li>
                </ol>
            </nav>);
    }
}

export default NavMaterials;


