import React, {Component} from 'react';
import CheckBoxList from './CheckBoxList';
import NavMaterials from './NavMaterials';

class Profiles extends Component {
    constructor(props) {
        super(props);
        this.state = {
            checkBoxs: [
                {
                    label: '', items:
                        [{
                            label: '-',
                            isChecked: false,
                            isView: true
                        }, {
                            label: '2-x ',
                            isChecked: false,
                            isView: true
                        }, {
                            label: '-',
                            isChecked: false,
                            isView: true
                        }, {
                            label: 'Z-',
                            isChecked: false,
                            isView: false
                        }, {
                            label: '-',
                            isChecked: false,
                            isView: false
                        }, {
                            label: 'T-',
                            isChecked: false,
                            isView: true
                        },]
                }, {
                    label: '', items:
                        [{
                            label: '',
                            isChecked: false,
                            isView: true
                        }, {
                            label: '',
                            isChecked: false,
                            isView: true
                        }, {
                            label: '',
                            isChecked: false,
                            isView: true
                        },]
                }, {
                    label: '', items:
                        [{
                            label: '',
                            isChecked: false,
                            isView: true
                        }, {
                            label: '',
                            isChecked: false,
                            isView: true
                        }, {
                            label: 'ALT C43',
                            isChecked: false,
                            isView: false
                        }, {
                            label: 'ALT C48',
                            isChecked: false,
                            isView: true
                        }, {
                            label: 'STATUS',
                            isChecked: false,
                            isView: true
                        }, {
                            label: 'ALBOX',
                            isChecked: false,
                            isView: true
                        }]
                }, {
                    label: '', items:
                        [{
                            label: 'Alutech',
                            isChecked: false,
                            isView: true
                        }, {
                            label: 'Realit',
                            isChecked: false,
                            isView: true
                        }, {
                            label: 'Titan',
                            isChecked: false,
                            isView: true
                        }, {
                            label: '',
                            isChecked: false,
                            isView: true
                        }]
                }, {
                    label: '', items:
                        [{
                            label: 'RAL',
                            isChecked: false,
                            isView: true
                        }, {
                            label: '',
                            isChecked: false,
                            isView: true
                        }, {
                            label: '',
                            isChecked: false,
                            isView: true
                        }]
                }]
        };
        this.onChecked = this.onChecked.bind(this);
    }

    onChecked(e) {
        let name = e.target.getAttribute('data-label');
        let val = e.target.value;
        let index = this.state.checkBoxs.findIndex((i) => i.label === name);
        let copyArr = this.state.checkBoxs;
        copyArr[index].items.map(i => {
            if (i.label === val) {
                i.isChecked = i.isChecked ? false : true
            }
            return i;
        });
        this.setState({
            checkBoxs: copyArr
        });
    }

    checkList() {
        return this.state.checkBoxs.map((i, index) => <CheckBoxList key={index} obj={i} onChecked={this.onChecked}/>)
    }

    styleImg = {
        width: '100px'
    };

    render() {
        return (
            <div className={'container-fluid'}>
                <div className={'row'}>
                    <div className="col-md-12">
                        <NavMaterials/>
                    </div>
                    <div className="col-md-2">
                        {this.checkList()}
                    </div>
                    <div className="col-md-10">
                        <table className="table table-borderless table-hover">
                            <thead>
                            <tr>
                                <th scope="col"></th>
                                <th scope="col"></th>
                                <th scope="col"></th>
                                <th scope="col"></th>
                                <th scope="col"> (x)</th>
                                <th scope="col"></th>
                                <th scope="col"></th>
                                <th scope="col"></th>
                                <th scope="col"></th>
                                <th scope="col"></th>
                            </tr>
                            </thead>
                            <tbody>
                            <tr>
                                <th scope="row">T-23</th>
                                <td>2-</td>
                                <td></td>
                                <td> 2-</td>
                                <td>45x45 </td>
                                <td>6 </td>
                                <td> RAL</td>
                                <td></td>
                                <td><img style={this.styleImg} src="/img/Optima1 (1).png"/></td>
                                <td><img style={this.styleImg} src="/img/Optima1 (1).png"/></td>
                            </tr>
                            <tr>
                                <th scope="row">T-23</th>
                                <td>2-</td>
                                <td></td>
                                <td> 2-</td>
                                <td>45x45 </td>
                                <td>6 </td>
                                <td> RAL</td>
                                <td></td>
                                <td><img style={this.styleImg} src="/img/Optima1 (1).png"/></td>
                                <td><img style={this.styleImg} src="/img/Optima1 (1).png"/></td>
                            </tr>
                            <tr>
                                <th scope="row">T-23</th>
                                <td>2-</td>
                                <td></td>
                                <td> 2-</td>
                                <td>45x45 </td>
                                <td>6 </td>
                                <td> RAL</td>
                                <td></td>
                                <td><img style={this.styleImg} src="/img/Optima1 (1).png"/></td>
                                <td><img style={this.styleImg} src="/img/Optima1 (1).png"/></td>
                            </tr>
                            <tr>
                                <th scope="row">T-23</th>
                                <td>2-</td>
                                <td></td>
                                <td> 2-</td>
                                <td>45x45 </td>
                                <td>6 </td>
                                <td> RAL</td>
                                <td></td>
                                <td><img style={this.styleImg} src="/img/Optima1 (1).png"/></td>
                                <td><img style={this.styleImg} src="/img/Optima1 (1).png"/></td>
                            </tr>
                            <tr>
                                <th scope="row">T-23</th>
                                <td>2-</td>
                                <td></td>
                                <td> 2-</td>
                                <td>45x45 </td>
                                <td>6 </td>
                                <td> RAL</td>
                                <td></td>
                                <td><img style={this.styleImg} src="/img/Optima1 (1).png"/></td>
                                <td><img style={this.styleImg} src="/img/Optima1 (1).png"/></td>
                            </tr>
                            <tr>
                                <th scope="row">T-23</th>
                                <td>2-</td>
                                <td></td>
                                <td> 2-</td>
                                <td>45x45 </td>
                                <td>6 </td>
                                <td> RAL</td>
                                <td></td>
                                <td><img style={this.styleImg} src="/img/Optima1 (1).png"/></td>
                                <td><img style={this.styleImg} src="/img/Optima1 (1).png"/></td>
                            </tr>
                            </tbody>
                        </table>
                    </div>
                </div>
            </div>);
    }
}

export default Profiles;


