import React, {Component} from 'react';
import CheckBoxList from './CheckBoxList';
import NavMaterials from './NavMaterials';

class Fill extends Component {
    constructor(props) {
        super(props);
        this.state = {
            checkBoxs: [
                {
                    label: '', items:
                        [{
                            label: '',
                            isChecked: false,
                            isView: true
                        }, {
                            label: '',
                            isChecked: false,
                            isView: true
                        }, {
                            label: '',
                            isChecked: false,
                            isView: true
                        }, {
                            label: '',
                            isChecked: false,
                            isView: false
                        }, {
                            label: '',
                            isChecked: false,
                            isView: false
                        }, {
                            label: '',
                            isChecked: false,
                            isView: true
                        }, {
                            label: '',
                            isChecked: false,
                            isView: true
                        }, {
                            label: '',
                            isChecked: false,
                            isView: true
                        }, {
                            label: '',
                            isChecked: false,
                            isView: true
                        }]
                }, {
                    label: '', items:
                        [{
                            label: '4 ',
                            isChecked: false,
                            isView: true
                        }, {
                            label: '5 ',
                            isChecked: false,
                            isView: true
                        }, {
                            label: '6 ',
                            isChecked: false,
                            isView: false
                        }, {
                            label: '8 ',
                            isChecked: false,
                            isView: true
                        }, {
                            label: '10 ',
                            isChecked: false,
                            isView: false
                        }, {
                            label: '12 ',
                            isChecked: false,
                            isView: false
                        }, {
                            label: '16 ',
                            isChecked: false,
                            isView: true
                        }, {
                            label: '18 ',
                            isChecked: false,
                            isView: false
                        }, {
                            label: '20 ',
                            isChecked: false,
                            isView: false
                        }, {
                            label: '24 ',
                            isChecked: false,
                            isView: true
                        }, {
                            label: '32 ',
                            isChecked: false,
                            isView: true
                        }, {
                            label: '34 ',
                            isChecked: false,
                            isView: false
                        }, {
                            label: '36 ',
                            isChecked: false,
                            isView: false
                        }, {
                            label: '44 ',
                            isChecked: false,
                            isView: false
                        }]
                }, {
                    label: '', items:
                        [{
                            label: '',
                            isChecked: false,
                            isView: false
                        }, {
                            label: '',
                            isChecked: false,
                            isView: true
                        }, {
                            label: '',
                            isChecked: false,
                            isView: true
                        }, {
                            label: '  RAL',
                            isChecked: false,
                            isView: true
                        }, {
                            label: '  ',
                            isChecked: false,
                            isView: false
                        }, {
                            label: ' ',
                            isChecked: false,
                            isView: true
                        }, {
                            label: ' ',
                            isChecked: false,
                            isView: true
                        }, {
                            label: '  ',
                            isChecked: false,
                            isView: false
                        }]
                }, {
                    label: '', items:
                        [{
                            label: 'Alutech',
                            isChecked: false,
                            isView: true
                        }, {
                            label: 'Realit',
                            isChecked: false,
                            isView: true
                        }, {
                            label: 'Titan',
                            isChecked: false,
                            isView: true
                        }, {
                            label: '',
                            isChecked: false,
                            isView: true
                        }]
                }
            ]
        };
        this.onChecked = this.onChecked.bind(this);
    }

    onChecked(e) {
        let name = e.target.getAttribute('data-label');
        let val = e.target.value;
        let index = this.state.checkBoxs.findIndex((i) => i.label === name);
        let copyArr = this.state.checkBoxs;
        copyArr[index].items.map(i => {
            if (i.label === val) {
                i.isChecked = i.isChecked ? false : true
            }
            return i;
        });
        this.setState({
            checkBoxs: copyArr
        });
    }

    checkList() {
        return this.state.checkBoxs.map((i, index) => <CheckBoxList key={index} obj={i} onChecked={this.onChecked}/>)
    }

    styleImg = {
        width: '100px'
    };

    render() {
        return (
            <div className={'container-fluid'}>
                <div className={'row'}>
                    <div className="col-md-12">
                        <NavMaterials/>
                    </div>
                    <div className="col-md-2">
                        {this.checkList()}
                    </div>
                    <div className="col-md-10">
                        <table className="table table-borderless table-hover">
                            <thead>
                            <tr>
                                <th scope="col"></th>
                                <th scope="col"></th>
                                <th scope="col"></th>
                                <th scope="col"> (x)</th>
                                <th scope="col"></th>
                                <th scope="col"></th>
                                <th scope="col"></th>
                                <th scope="col"></th>
                            </tr>
                            </thead>
                            <tbody>
                            <tr>
                                <th scope="row">T-44994</th>
                                <td></td>
                                <td></td>
                                <td>2000x3000</td>
                                <td>4;5;6;8</td>
                                <td></td>
                                <td></td>
                                <td><img style={this.styleImg} src="/img/stekmat.png"/></td>
                            </tr>
                            </tbody>
                        </table>
                    </div>
                </div>
            </div>);
    }
}

export default Fill;


